@import '../../../static/breakpoints/breakpoints.scss';

.links {
  display: flex;
  align-items: center;
  gap: 50px;

  .links__sublist {
    display: flex;
    gap: 50px;
    flex-wrap: wrap;
    @media (max-width: $phone) {
      flex-direction: column;
    }
  }
  @media (max-width: $desktop-s) {
    padding: 0 32px;
    flex-wrap: wrap;
  }

  @media (max-width: $tablet) {
    padding: 0 12px;
  }
  .links__item {
    color: #1d1d1d;
    font-family: 'Cambria', Cochin, Georgia, Times, 'Times New Roman', serif;
    font-size: 24px;
    font-style: normal;
    font-weight: 700;
    line-height: 88.5%;
    letter-spacing: 0.48px;
    text-transform: uppercase;
    .links__link-button {
      background: none;
      border: none;
      color: #1d1d1d;
      font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
      font-size: 20px;
      font-style: normal;
      font-weight: 700;
      line-height: 88.5%;
      letter-spacing: 0.48px;
      text-transform: uppercase;
      cursor: pointer;

      &:hover {
        color: #e37099;
      }
    }
    &.links__item-bold {
      color: #1d1d1d;
      font-size: 48px;
      font-style: normal;
      font-weight: 400;
      letter-spacing: 1.4px;
      @media (max-width: $tablet) {
        font-size: 32px;
      }
    }
  }
}
