@import '../../static/breakpoints/breakpoints.scss';

.pictures {
  max-width: 1280px;
  margin: 0 auto;
  padding-top: 150px;
  padding-bottom: 150px;
  .pictures__list-wrapper {
    margin-top: 70px;
  }
  .pictures__switch-list {
    margin-top: 30px;

    @media (max-width: $desktop-s) {
      padding: 0 32px;
    }

    @media (max-width: $tablet) {
      padding: 0 12px;
    }
  }
}
