@import '../../static/breakpoints/breakpoints.scss';

.photo-list2 {
  display: flex;
  gap: 40px;
  max-width: 1280px;
  margin: 0 auto;
  .photo-list2__item {
    width: 25%;
    .photo-list2__image {
      width: 100%;
      height: 100%;
      max-height: 406px;
      object-fit: cover;
      object-position: bottom;
    }
  }
  @media (max-width: $desktop-s) {
    flex-direction: column;
    .photo-list2__item {
      width: 100%;
      .photo-list2__image {
        max-height: 100%;
      }
    }
  }
}
