@import '../../static/breakpoints/breakpoints.scss';

.title {
  margin-top: 140px;
  color: #f3f3f3;
  font-family: 'Baskerville Old Face';
  font-size: 90px;
  font-weight: 400;
  line-height: 88.5%;
  letter-spacing: 2.8px;
  text-transform: uppercase;
  .title__top {
    text-align: left;
  }
  .title__bottom {
    margin-top: 32px;
    text-align: right;
  }
  @media (max-width: $tablet) {
    font-size: 45px;
  }
}
