@import '../../static/breakpoints/breakpoints.scss';

.photo-list {
  display: flex;
  gap: 40px;
  max-width: 1280px;
  margin: 0 auto;
  .photo-list__item {
    width: 25%;
    .photo-list__image {
      width: 100%;
    }
  }
  @media (max-width: $desktop-s) {
    flex-direction: column;
    .photo-list__item {
      width: 100%;
    }
  }
}
