@import '../../static/breakpoints/breakpoints.scss';

.price-block2 {
  max-width: 1280px;
  margin: 0 auto;
  display: flex;
  align-content: center;
  justify-content: space-between;
  .price-block2__info {
    max-width: 800px;
    .price-block2__title {
      color: #fff;
      font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
      font-size: 48px;
      font-weight: 400;
      line-height: 88.5%;
      letter-spacing: 1.4px;
      text-transform: uppercase;
    }
    .price-block2__subtitle {
      display: block;
      margin-top: 80px;
      color: #fff;
      font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
      font-size: 32px;
      font-style: normal;
      font-weight: 700;
      line-height: 88.5%;
      letter-spacing: 0.8px;
      text-transform: uppercase;
    }
    .price-block2__list {
      margin-top: 60px;
      display: grid;
      grid-template-columns: 524px 230px;
      column-gap: 45px;
      row-gap: 40px;
      @media (max-width: $tablet) {
        column-gap: 18px;
      }
      span {
        display: flex;
        align-items: center;
        justify-content: space-between;
        color: #fff;
        font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
        font-size: 20px;
        font-weight: 700;
        letter-spacing: 0.4px;
        text-transform: uppercase;

        @media (max-width: $tablet) {
          font-size: 14px;
        }
      }
    }
  }
  .price-block2__image {
    max-width: 394px;
    display: block;
    width: 100%;
    object-fit: cover;
  }

  @media (max-width: $desktop-s) {
    display: flex;
    align-items: center;
    position: relative;
    .price-block2__info .price-block2__list {
      position: relative;
      z-index: 999;
      margin: 0;
      align-items: center;
      grid-template-columns: repeat(2, 1fr);
    }
    .price-block2__image {
      position: absolute;
      left: 0;
      opacity: 0.25;
    }
  }
}
