@import '../../static/breakpoints/breakpoints.scss';

.footer {
  display: flex;
  justify-content: space-between;
  align-items: center;
  max-width: 1280px;
  margin: 0 auto;
  padding-bottom: 40px;
  .footer__designer {
    color: #fff;
    font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
    font-size: 20px;
    font-weight: 400;
    line-height: 88.5%;
    text-transform: uppercase;
  }

  @media (max-width: $desktop-s) {
    flex-direction: column-reverse;
    gap: 24px;
    .photo-list__item {
      width: 100%;
    }
  }
}
