@import '../../static/breakpoints/breakpoints.scss';

.social-links {
  display: flex;
  align-items: center;
  gap: 40px;
  .social-links__link {
    &:hover {
      svg {
        * {
          fill: #e37099;
        }
      }
    }
  }

  @media (max-width: $desktop-s) {
    gap: 100px;
  }

  @media (max-width: $phone) {
    width: 100%;
    justify-content: space-between;
    gap: 0;
  }
}
